body {
  height: 100%;
}

.page {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 0;
  min-height: 100%;
  padding: 30px;
  border: 6px solid;
  background: #fff;
}

.main {
  margin: auto 0;
}

.page-background {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 500ms background;
}

.page-background::after {
  content: ' ';
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  background: rgba(42, 42, 41, 0.9);
}

