.mx-auto {
  margin-right: auto;
  margin-left: auto;
}

.header-logo {
  text-align: center;
}

.footer {
  color: #999;
}

button,
.btn,
input,
.input {
  padding: 10px 15px;
  background: none;
  border: 1px solid #999;
  line-height: 1.3;
  transition: all 150ms;
}

button:hover, button:focus,
.btn:hover,
.btn:focus,
input:hover,
input:focus,
.input:hover,
.input:focus {
  border-color: #2a2a29;
}

/**
 * Icons
 */
.icon {
  display: inline-block;
  height: 1em;
  vertical-align: middle;
}

/**
 * audioURL
 */
.audioURL-main {
  display: flex;
  margin: 0 auto 48px;
}

.audioURL-input {
  flex: 1 1 auto;
}

.audioURL-input:focus {
  outline: none;
  border-color: #2a2a29;
}

.audioURL-submit {
  flex: 0 0 auto;
  margin-left: 8px;
}

.audioURL-submit:hover, .audioURL-submit:focus {
  outline: none;
  border-color: #2a2a29;
  background: #2a2a29;
  color: #fff;
}

/**
 * Player
 */
.player {
  position: relative;
  z-index: 0;
}

.playerTitle a {
  text-decoration: none;
}

.playerControls {
  position: relative;
  z-index: 0;
  width: 24rem;
  height: 24rem;
  text-align: center;
  background-color: #2a2a29;
  overflow: hidden;
}

.playerControls-bg {
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-position: 50% 50%;
  background-size: cover;
  background-repeat: no-repeat;
  filter: blur(6px);
  transform: scale(1.1);
  transition: all 250ms;
}

.playerControl {
  display: inline-block;
  padding-top: 0;
  padding-bottom: 0;
  border: none;
  border-radius: 100%;
  font-size: 2rem;
  outline: none;
  background: #fff;
}

.playerControl:not(:last-child) {
  margin-right: .5rem;
}

.playerControl .icon {
  vertical-align: -0.15625em;
}

.playerControl .icon-play {
  margin-right: -0.15625em;
}

.playerControl[disabled] {
  opacity: 0.2;
}

.playerControl[disabled]:hover {
  color: inherit;
  border-color: inherit;
}

.playerControl--next,
.playerControl--previous {
  width: 4rem;
  height: 4rem;
}

.playerControl--play {
  width: 8rem;
  height: 8rem;
}

/**
 * Playlist
 */
.playlistTrack {
  cursor: pointer;
}

.playlistTrack:hover {
  background: whitesmoke;
}

.playlistTrack.is-active {
  background: whitesmoke;
}

.playlistTrack-indicator .icon {
  display: none;
}

.playlistTrack.is-active .playlistTrack-indicator .icon-play {
  display: inline-block;
}
